import styles from "./DemoWidget.module.scss";

type Props = {
  settings: {
    widgetHeaderColor: string;
    widgetBackgroundColor: string;
    widgetBorderRadius: string;
    widgetBorderColor: string;
    widgetBorderWidth: string;
  };
};

const DemoWidget: React.FC<Props> = ({ settings }) => {
  const {
    widgetHeaderColor,
    widgetBackgroundColor,
    widgetBorderRadius,
    widgetBorderColor,
    widgetBorderWidth,
  } = settings;
  return (
    <div
      className={styles.content_insight_chat_popup}
      style={{
        backgroundColor: widgetBackgroundColor,
        borderRadius: widgetBorderRadius + "px",
        borderColor: widgetBorderColor,
        borderWidth: widgetBorderWidth + "px",
      }}
    >
      <div
        className={styles.content_insight_header}
        style={{ backgroundColor: widgetHeaderColor }}
      >
        Help
        <button
          className={styles.content_insight_toggle_button}
          title="open / close"
        >          _
        </button>
      </div>
      <div className={styles.content_insight_content}>
        <div className={styles.content_insight_input_wrapper}>
          <svg width="16" height="16" viewBox="0 0 16 16" focusable="false">
            <circle cx="6" cy="6" r="5.5" fill="none" stroke="black"></circle>
            <path stroke="black" strokeLinecap="round" d="M15 15l-5-5"></path>
          </svg>
          <input
            className={styles.content_insight_question_input}
            placeholder="How can we help?"
          />
          <button className={styles.content_insight_clear_button}>
            <svg width="16" height="16" viewBox="0 0 16 16" focusable="false">
              <path
                stroke="black"
                strokeLinecap="round"
                d="M3 13L13 3m0 10L3 3"
              ></path>
            </svg>
          </button>
        </div>
        <button
          title="Send question request"
          className={styles.content_insight_ask_button}
        >
          Ask!
        </button>
        <div className="content_insight_response_block"></div>
      </div>
    </div>
  );
};

export default DemoWidget;
